import { createContext, memo, useEffect, useReducer } from "react";
import { ActivePensum, activePensumReducer, loadPensumFromLocalStorage } from "./local-storage";

type Obj = {
  state: Pensum.Pensum | null,
  dispatch: React.Dispatch<ActivePensum.Action>,
}

const ActivePensumContext = createContext({} as Obj);


type Props = { children: any};

export const ActivePensumProvider = memo(({ children }: Props) => {
  const [state, dispatch] = useReducer(activePensumReducer, null);

  // onMount: load saved pensum
  useEffect(() => {
    dispatch({type: 'load'});
  }, []);
  
  return <ActivePensumContext.Provider value={{ state, dispatch }}>
    {children}
  </ActivePensumContext.Provider>
})


export default ActivePensumContext;